
// @import 'base16/base16-paraiso.scss';
@import 'base16/base16-marrakesh.scss';
@import "https://lf9-cdn-tos.bytecdntp.com/cdn/expire-1-M/font-awesome/6.0.0/css/all.css";

$note-margin: 15px;


.post .content a {
    text-decoration-line: underline !important;
}

// fa 和 fa-solid是复现了 fontanswomw的fa-solide，便于在此复用
@mixin fa{
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: var(--fa-display, inline-block);
    font-style: normal;
    font-variant: normal;
    line-height: 1;
    text-rendering: auto;
}

@mixin fa-solid{
    @include fa;
    font-family: 'Font Awesome 6 Free';
    font-weight: 900;
}


@mixin note {
    @include fa-solid ;
    margin-right: $note-margin ;
    font-size:60px;
}


//大部份的class可以直接使用 markdown的```块语法，然后添加对应的 .class_name 来实现

//配方 formula name
.formula{
    background-color: $base16-color-0b;
    width: 50%;
    padding: 10px;
    display: block;
    margin: 0px;
    font-weight: bold;
    border-radius: 15px;
}

.formula::before{
    @include note ;
    color:  $base16-color-0a;
    content: "\211E";
}


.herb {
    background-color: $base16-color-0d;
    display: block;
    width: 50%;
    padding: 20px;
    font-weight: bold;
    border-radius: 10px;
}



//图形，见： https://cloud.tencent.com/developer/article/2144762

.herb::before{
    @include note ;
    color:  #1cca1c;
    content: "\2618";

}



//引用
.quote {
    display: block;
    width: 80%;
    white-space: normal;
    margin-left: 30px;
    border: 1px dashed rgb(55, 138, 52);
    border-left: 0px solid  #ffffff;
    font-weight: bold ;
}


//重点
.emphasis {
    font-weight: bolder;
    display: block;
    width: 80%;
    white-space: normal;
    border: 1px dashed rgb(55, 138, 52);
    border-left: 5px solid  #ffffff ;
}
.emphasis::before {
    @include note ;
    font-size:30px;
    color:  $base16-color-08;
    content: "\2611";

}


//注释
.comment {

    margin-left: 30px;
    display: block;
    width: 80%;
    white-space: normal;
    border: 1px dashed rgb(55, 138, 52);
    border-left: 5px solid  rgb(77, 160, 74);
    border-radius: 2px;
    padding: 15px 25px 15px;
}

.comment::before{
    @include note ;
    color:  $base16-color-09;
    content: "\270E";
}

